.nav-buttons {
  padding: 10px;
}
nav.disable {
  pointer-events: none;
}

/* Global */
.alert.alert-warning, .breadcrumb {
  border-radius: 0px;
}
.table thead th, .table thead tr, .table-bordered {
  border-width: 0px;
}
tbody tr th, .table .thead-dark th {
  font-weight: 500;
}
.text-muted {
  font-weight: 400;
}

.thead-orange {
  background-color: #f59525;
  color: white; 
}

.btn-orange {
  color: white;
  background-color: #f59525;
}
.btn-orange:hover {
  background-color: white;
  color: #f59525;
}

.btn-white {
  background-color: white;
  color: #f59525;
}
.btn-white:hover {
  color: white;
  background-color: #f59525;
}

a.nav-items:link, a.nav-items:visited, a.nav-link:link, a.nav-link:visited, .card-title {
  color: #f59525;
}

a.nav-link:hover {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.title-banner {
  color: white;
}

.white-table {
  background-color: white;
}
.white-table > .needs-validation > .table {
  margin-bottom: 0px;
}

.bottom-panel {
  /* background-color: #f59525; */
  background-color: #fff;
  color: #333;
  margin-top: 20px;
  margin-bottom: 20px;
}

.breadcrumb {
  background-color: white;
}

.center-container {
  background-color: #99C24D;
  width: 1165px;
  padding-bottom: 5px;
}

body {
  background-color: lightgray;
}

.summary-cards {
  /* margin-top: 15px;
  margin-bottom: 10px; */
}

.alert-success {
  margin-right: 10px;
  margin-left: 10px;
}

