/* SaladBox */
.wrap-select-extra {
  margin-top: 10px;
  padding: 5px;
  min-width: 300px;
  max-width: 500px;
}
.wrap-select-extra.primary {
  background-color: #cce5ff;
  border-color: #b8daff;
}
.wrap-select-extra.warning {
  background-color: #f8d7da;
  border-color: #f5c6cb;
}
.select-dressing {

}
.saladbox-badge {
  margin-bottom: 3px;
  vertical-align: top;
  padding: 0px 0px 0px 5px;
  margin-right: 5px;
}
.badge-name {
  display: inline-block;
  padding-right: 5px;
  height: 30px;
  /* height: 24px;
  line-height: 24px; */
  line-height: 30px;
  font-size: 1.2em;
  vertical-align: top;
  border-right: 2px solid #b3b3b3;
}
.badge-name > .badge {
  margin-left: 5px;
  padding: 4px;
  font-size: 1em;
  line-height: 10px;
}
.dressing-value-btn, .saladbox-badge .close {
  display: inline-block;
  /* height: 30px; */
  line-height: 27px;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: .5;
  padding: 0;
  background-color: transparent;
  border: 0;
  -webkit-appearance: none;
}
.saladbox-badge .close {
  padding-left: 5px;
  padding-right: 5px;
  height: 30px;
  font-size: 1rem;
  font-weight: 500;
  opacity: 1 !important;
  color: #fff;
}
.saladbox-badge .close:hover {
  background-color: #fff;
}
.dressing-value-btn.add {
  padding-left: 5px;
  padding-right: 5px;
  border-left: 1px solid #fff;
}
.dressing-value-btn.subtract {
  padding-right: 5px;
  padding-left: 5px;
  /* border-left: 1px solid #fff; */
}

/* Summary */
.summary-cards.card {
  border-width: 0px;
}
.summary-cards .nav-link {
  color: #dc3545 !important;
  font-weight: 600;
}
.summary-cards > .card-body {
  padding-top: 5px;
}
.summary-cards .card-title {
  color: #333;
  font-weight: 400;
  font-size: 1.4rem;
  text-align: left;
}
.summary-cards .card-header:first-child {
  padding-top: 0px;
  padding-bottom: 0px;
  border-top: 1px solid #d6d6d6;
  border-bottom: 0px solid rgba(0,0,0,.125);
  border-radius: 0px;
  background-color: inherit;
}
.summary-cards table td:first-child, .summary-cards table th:first-child {
  text-align: left;
}
.summary-cards thead {
  background-color: #f3f3f3;
}
.summary-cards .table thead th {
  padding-top: 7px;
  padding-bottom: 7px;
  vertical-align: bottom;
  border-bottom: 2px solid #dee2e6;
  font-weight: 500;
}
.summary-cards .thead-orange {
  background-color: #f9cd9b;
  color: #1f1f1f;
}
.summary-cards .table tbody td {
  padding-top: 6px;
  padding-bottom: 6px;
}


